<template>
  <el-tabs v-model="activeName" @tab-click="handleClick">
    <el-tab-pane label="认证转化率统计" name="first">
      <div class="p_6">
        <div class="search-wrapper">
          <div class="search-content">
            <el-select
              v-model="searchParam.type"
              clearable
              slot="prepend"
              placeholder="渠道"
              style="width: 120px;"
            >
              <el-option
                v-for="item in typeOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
            <el-select
              v-model="searchParam.device"
              slot="prepend"
              clearable
              placeholder="设备"
              style="width: 120px;"
            >
              <el-option
                v-for="item in deviceOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
            <el-date-picker v-model="searchParam.dateRange" type="daterange" placeholder="选择日期范围"></el-date-picker>
            <el-select
              v-model="searchParam.rate"
              :multiple-limit="5"
              multiple
              collapse-tags
              placeholder="请选择统计选项"
            >
              <el-option
                v-for="item in countOptions"
                :key="item.value"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <div class="search-btn">
            <el-button slot="append" type="primary" icon="search" @click="search">查询</el-button>
          </div>
        </div>
        <div class="chart-container">
          <chart
            :options="line"
            ref="operLine"
            theme="ovilia-green"
            auto-resize
            class="operChartClass"
          ></chart>
          <el-alert
            title
            type="info"
            description="提示：图表下方和右侧灰色区域两头可拖动控制相应联动区域的缩小和放大，方便数据查看，光标在图表区域内鼠标滑轮滑动亦可以！"
            show-icon
            :closable="closable"
          ></el-alert>
          <el-alert
            title
            description="警告：图表右上角工具条数据视图点开情况下，请勿点击查询按钮，如点击查询再点击数据视图内刷新后会与前一查询结果重合且图表类型变化，可点击工具条还原按钮还原！"
            show-icon
            type="warning"
            :closable="closable"
          ></el-alert>
        </div>
      </div>
    </el-tab-pane>
  </el-tabs>
</template>

<style scoped>
.el-select .el-input {
  width: 100px;
}

date-range:after {
  display: block;
  content: ".";
  line-height: 1;
  clear: both;
  visibility: hidden;
}

.operChartClass {
  width: 100%;
  height: 100%;
}
</style>

<script>
import Common from "../../../utils/common";
import auth from "../../../utils/authenticate";
import { Notification } from "element-ui";

import "echarts/lib/component/grid";
import "echarts/lib/component/toolbox";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/title";
import "echarts/lib/component/legend";
import "echarts/lib/chart/bar";

// built-in theme
import "echarts/theme/dark";

import $ from "jquery";
import deepcopy from "deepcopy";
import formatDate from "../../../utils/formatDate.js";
import initChartData from "../../../data/initChart";
export default {
  data() {
    let date = new Date();
    let prevMonth = new Date(date.setDate(date.getDate() - 30));
    let now = new Date();

    return {
      closable: true,
      typeOptions: [
        {
          value: "ALL",
          name: "ALL",
        },
        {
          value: "市场厂商",
          name: "市场厂商",
        },
        {
          value: "H5渠道",
          name: "H5渠道",
        },
      ],
      deviceOptions: [
        { value: "All", name: "All" },
        { value: "iOS", name: "iOS" },
        { value: "Android", name: "Android" },
      ],
      countOptions: [
        { value: "loan_info", name: "借款申请" },
        { value: "pid_info", name: "身份证" },
        { value: "base_info", name: "基本信息" },
        { value: "zhima_info", name: "芝麻" },
        { value: "link_info", name: "联系人" },
        { value: "phone_info", name: "运营商" },
        { value: "bank_info", name: "银行卡" },
        { value: "taobao_info", name: "淘宝" },
        { value: "zhifubao_info", name: "支付宝" },
      ],
      searchParam: {
        device: "",
        dateRange: [prevMonth, now],
        time: "",
        unit: "",
        rate: "",
        type: "",
      },
      activeName: "first",
      select: "",
      listLoading: true,
      line: {
        series: [],
      },
      lineGroup: {},
      line: deepcopy(initChartData),
    };
  },
  methods: {
    clearLine() {
      this.line.legend.data = [];
      this.line.xAxis && (this.line.xAxis[0].data = []);
      this.line.series = [];
      this.line.yAxis[0].axisLabel = {
        formatter: "{value}",
      };
      //格式化tooltip的坐标轴指示器显示数值
      this.line.tooltip.formatter = null;
      this.line.tooltip.axisPointer.label = { formatter: "{value}" };
    },
    setDataView(categorys, xAxisData) {
      var thead = "",
        trTd = [],
        tbodyTr = "";
      for (var i = 0; i < categorys.length; i++) {
        thead += "<td>" + this.line.series[i].name + "</td>"; //头部
      }

      for (var j = 0, l = xAxisData.length; j < l; j++) {
        trTd[j] = "";
        for (var i = 0; i < categorys.length; i++) {
          trTd[j] += "<td>" + this.line.series[i].data[j] + "</td>";
        }

        tbodyTr += "<tr>" + "<td>" + xAxisData[j] + "</td>" + trTd[j] + "</tr>";
      }
      this.line.toolbox.feature.dataView.optionToContent = function() {
        var table =
          '<table style="width:100%;text-align:center"><tbody><tr>' +
          "<td>时间</td>" +
          thead +
          "</tr>" +
          tbodyTr +
          "</tbody></table>";
        return table;
      };
    },
    setOptions(categorys, xAxisData) {
      let len = xAxisData.length;
      let colors = [
        "rgba(255,144,128,1)",
        "rgba(0,191,183,1)",
        "rgba(252,230,48,1)",
        "rgba(255,73,73,1)",
        "rgba(29,144,230,1)",
      ];
      this.line.xAxis[0].data = xAxisData;
      this.line.legend.data = categorys.map(item => {
        return item.value;
      });
      for (var i = 0; i < categorys.length; i++) {
        this.lineGroup[categorys[i].value] = i;
        let seriesOptions = {
          name: categorys[i].value,
          type: "line",
          barMaxWidth: 30,
          barGap: "80%",
          symbolSize: 5,
          symbol: "circle",
          data: Array(len).fill(0),
          itemStyle: {
            normal: {
              color: colors[i],
              label: {
                show: true,
                position: "insideTop",
                formatter(p) {
                  return p.value > 0 ? p.value : "";
                },
              },
            },
          },
        };
        this.line.series.push(seriesOptions);
      }
    },
    search() {
      let self = this;

      let dateRange = self.searchParam.dateRange;
      let line = this.$refs.operLine;
      line.showLoading({
        text: "正在加载",
        color: "#4ea397",
        maskColor: "rgba(255, 255, 255, 0.4)",
      });
      this.listLoading = true;
      let ajaxUrl = "/web/v1/operation/chart/queryAuthProcessDayChart";
      let paramData = {
        device: self.searchParam.device,
        beginTime: formatDate(dateRange[0], "yyyy-MM-dd") + " 00:00:00",
        endTime: formatDate(dateRange[1], "yyyy-MM-dd") + " 23:59:59",
        rate: self.searchParam.rate,
        type: self.searchParam.type,
      };
      Common.ajax({
        url: ajaxUrl,
        type: "POST",
        contentType: "application/json",
        data: paramData,
        success: response => {
          this.$refs.operLine.hideLoading();
          self.listLoading = false;
          if (response.data && response.data.list) {
            this.clearLine();
            let resData = response.data;
            this.setOptions(resData.categories, resData.dates);
            resData.list.forEach((item, index, arr) => {
              let dataIdx = this.line.xAxis[0].data.indexOf(item.date);
              if (dataIdx >= 0) {
                resData.categories.forEach(option => {
                  this.line.series[this.lineGroup[option.value]].data[dataIdx] =
                    item[option.key];
                });
              }
            });
            this.setDataView(resData.categories, resData.dates);
          }
        },
        error: err => {
          this.$refs.operLine.hideLoading();
          self.listLoading = false;
          let error = err.message || err;
          Notification.error({
            title: "错误",
            message: error,
          });
        },
      });
    },
    handleClick(tab, event) {
      let index = tab.index;
      if (index == 0) {
        this.activeName = "first";
      }
    },
  },
  mounted() {
    let cvHeight = $(".contents").height() - 110;
    $(".operChartClass").css("height", cvHeight);

    //调用echarts实例resize函数
    this.$refs.operLine.resize();

    this.search();
    this.line.title.text = "认证转化率统计(日)";
    this.line.xAxis[0].name = "时间/日";
    this.line.yAxis[0].name = "转化率/%";
  },
  beforeDestroy() {
    this.clearLine();
  },
};
</script>
